#end {
  text-align: center;
  position: absolute;
  width: 100%;
  top: 60%;
  display: inline-block;
  font-size: 2em;
}
#end p {
  font-family: sans-serif;
  color: pink;
  font-size: 30px;
  font-weight: bold;
  margin: 20px auto;
  text-align: center;
  animation: text 0.5s linear 0.4s;
  -webkit-animation: text 0.4s linear 0.3s;
}
