@charset "UTF-8";
#stack {
  cursor: pointer;
  border-radius: 10px;
  position: relative;
  box-sizing: border-box;
  padding: 10px;
  box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.2);
  transform: rotate(2deg);
}
#stack #before {
  top: -2px;
  left: -2px;
  transform: rotate(-1deg);
}
#stack #after {
  top: -5px;
  left: -6px;
  transform: rotate(-1.5deg);
}
#stack #before,
#stack #after {
  content: "";
  height: 174px;
  width: 114px;
  display: block;
  border-radius: 10px;
  position: absolute;
  box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.2);
}
#stack #card {
  width: 114px;
  height: 174px;
  position: absolute;
  transform-style: preserve-3d;
  transition: transform 0.4s ease, margin 0.6s ease 0.2s;
  left: -3px;
  top: -3px;
}
#stack #card figure {
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  border-radius: 10px;
  transition-delay: transform 1s;
}
#stack #card figure.front {
  background: #fafafa;
  transform: rotateZ(2deg) rotateY(180deg);
}
#stack #card figure.front:after {
  color: #555;
  font-size: 120px;
  font-family: arial;
  position: absolute;
  top: 80px;
}
#stack.animate #card {
  margin: -8px 0px 0px 200px;
  transform: rotateY(-180deg);
  transition: transform 0.6s ease 0.4s, margin 0.8s ease;
}
#stack.animate #card figure {
  box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.2);
}
